h1 {
    font-size: 40px;
}
.big-logo img {
    width: 250px;
}
.big-logo {
    position: relative;
    z-index: 55;
}
.c {
    color: #00a1da;
    position: absolute;
    left: 10px;
    top: 100px;
    animation-name: fly;
    animation-duration: 7s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    z-index: -1;
    text-shadow: #00a1da 0px 0px 15px;
}
@keyframes fly {
    0% {
        left: 5%;
        top: 100px;       
    }
    25% {
        left: 7%;
        top: 120px;       
    }
    50% {
        left: 9%;
        top: 140px;       
    }
    75% {
        left: 11%;
        top: 160px;       
    }
    100% {
        left: 15%;
        top: 180px;       
    }
}
